import React, { Component } from "react";
import { Router, Switch, Route, Redirect } from "react-router-dom";
import { createBrowserHistory } from "history";

import { to, routes } from "./platform/routes";
import Layout from "./components/layout";

import "./assets/styles/index.scss";

class App extends Component {
  state = {
    generalAPILoaded: false
    // loader: true,
    // content: false,
  };

  componentDidMount() {
    //? For SSR to fully load Browser API (general for 'window')
    window.routerHistory = createBrowserHistory();
    this.setState({ generalAPILoaded: true });
    // setTimeout(() => this.setState({ loader: false }), 6000);
    // setTimeout(() => this.setState({ content: true }), 5000);
  }

  render() {
    const { generalAPILoaded } = this.state;

    return generalAPILoaded ? (
      <Router history={window.routerHistory}>
        <Layout>
          <Switch>
            {routes.map((item, i) => (
              <Route
                key={i}
                exact={item.exact}
                component={item.component}
                path={item.path}
              />
            ))}

            <Redirect to={to.home} />
          </Switch>
        </Layout>
      </Router>
    ) : null;
  }
}

export default App;
